@import "../components/variables";

@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    .medical__devices {
      .container {
        grid-template-columns: 25% 55% 20%;
        .content__popularProducts {
          ul {
            li {
              width: 230px;
              height: 290px;
            }
            li {
              img {
                width: 150px;
                height: 150px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1025px), screen and (max-device-width: 1025px){
  body{
    .medical__devices{
      .container{
        grid-template-columns: 30% 70%;
        grid-template-areas: "aside breadcrumb"
                             "aside catalog"
                             "order order"
                             "popProd popProd";
        .content__catalog{
          &__medicalDevices__info{
            padding-top: 55px;
            p{
              padding: 0 50px 0 0;
            }
          }
        }
        .content__popularProducts{
          padding-top: 0;
          a{
            width: 100%;
            font-size: 1.25em;
            line-height: 1.375em;
          }
          ul{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 840px), screen and (max-device-width: 840px){
  body{
    .medical__devices{
      .container{
        .content__popularProducts{
          ul{
            li{
              width: 200px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .medical__devices{
      .container{
        grid-template-areas: "breadcrumb breadcrumb"
                             "catalog catalog"
                             "aside aside"
                             "popProd popProd"
                             "order order";
        .content__popularProducts{
          ul{
            li{
              width: 30%;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 670px), screen and (max-device-width: 670px){
  body{
    .medical__devices{
      .container{
        .content__catalog{
          &__medicalDevices__info{
            p{
              padding: 0;
            }
          }
        }
        .content__popularProducts{
          ul{
            li{
              width: 45%;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 450px), screen and (max-device-width: 450px){
  body{
    .medical__devices{
      .container{
        .content__popularProducts{
          ul{
            li{
              width: 90%;
            }
          }
        }
      }
    }
  }
}